.Scrollable {
  position: relative;
  overflow: hidden;
  height: 100%;
}

.Scrollable::before {
  content: '';
  display: block;
  position: absolute;
  z-index: 1;
  top: 0;
  height: 3em;
  width: 100%;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(20%, white), to(transparent));
  background: -webkit-linear-gradient(white 20%, transparent);
  background: -o-linear-gradient(white 20%, transparent);
  background: linear-gradient(white 20%, transparent);
}

.Scrollable::after {
  content: '';
  display: block;
  position: absolute;
  z-index: 1;
  bottom: 0;
  height: 3em;
  width: 100%;
  background: -webkit-gradient(
    linear,
    left bottom, left top,
    color-stop(20%, white),
    to(transparent)
  );
  background: -webkit-linear-gradient(
    bottom,
    white 20%,
    transparent
  );
  background: -o-linear-gradient(
    bottom,
    white 20%,
    transparent
  );
  background: linear-gradient(
    to top,
    white 20%,
    transparent
  );
}
.Scrollable-Wrapper {
  position: relative;
  overflow-y: auto;
  padding: 3em 0;
  height: 100%;
}



